import { post } from "../../utils/request";
import url from "../../utils/url";
import { actions as appActions } from "./app";

const initialState = {
  userId: null,
  username: null
};

// action types
export const types = {
  LOGIN: "AUTH/LOGIN",    //
  LOGOUT: "AUTH/LOGOUT"   //
};

// action creators
export const actions = {
  // action
  login: (username, password) => {
    return dispatch => {
      // APIappstartRequest action
      dispatch(appActions.startRequest());
      const params = { username, password };
      return post(url.login(), params).then(data => {
        // APIappfinishRequest action
        dispatch(appActions.finishRequest());
        // 
        if (!data.error) {
          dispatch(actions.setLoginInfo(data.userId, username));
        } else {
          dispatch(appActions.setError(data.error));
        }
      });
    };
  },
  logout: () => ({
    type: types.LOGOUT
  }),
  setLoginInfo: (userId, username) => ({
    type: types.LOGIN,
    userId: userId,
    username: username
  })
};

// reducers
const reducer = (state = initialState, action) => {
  switch (action.type) {
    case types.LOGIN:
      return { ...state, userId: action.userId, username: action.username };
    case types.LOGOUT:
      return { ...state, userId: null, username: null };
    default:
      return state;
  }
};

export default reducer;

// selectors
export const getLoggedUser = state => state.auth;



